import {Dispatch} from 'redux'
import {appSetStatus, AppSetStatusType, SetAppErrorType} from "../../app/App-reducer";
import {authApi, LoginParamsType} from "../../api/todolists-api";


const initialState = {
    isLoggedIn: false
}
type InitialStateType = typeof initialState

export const loginReducer = (state: InitialStateType = initialState, action: ActionsType): InitialStateType => {
    switch (action.type) {
        case 'login/SET-IS-LOGGED-IN':
            return {...state, isLoggedIn: action.value}
        default:
            return state
    }
}
// actions
export const setIsLoggedInAC = (value: boolean) =>
    ({type: 'login/SET-IS-LOGGED-IN', value} as const)

// thunks
export const loginTC = (data: LoginParamsType) => (dispatch: Dispatch<ActionsType>) => {
     dispatch(appSetStatus('loading'))
    authApi.login(data)
        .then(res => {
            if (res.data.resultCode === 0) {
                dispatch(setIsLoggedInAC(true))
                dispatch(appSetStatus('idle'))
            }
        })
}

export const logoutTC = () => (dispatch: Dispatch<ActionsType>) => {
    dispatch(appSetStatus('loading'))
    authApi.logout()
        .then(res => {
            if(res.data.resultCode === 0) {
                dispatch(setIsLoggedInAC(false))
                dispatch(appSetStatus('succeeded'))
            }
        })
}

// types
type ActionsType = ReturnType<typeof setIsLoggedInAC> | AppSetStatusType | SetAppErrorType
